<script>
import { Toast } from "vant";
export default {
  props: {},
  data() {
    return {
      lists: {},
      day: 0,
      hr: 0,
      min: 0,
      sec: 0,
      freight: 12,
      freights: 26,
      show: false,
      goods: {},

      goodsId: "",
      quota: 0,
      quotaUsed: 0,

      sku: {
        // 所有sku规格类目与其值的从属关系，比如商品有颜色和尺码两大类规格，颜色下面又有红色和蓝色两个规格值。
        // 可以理解为一个商品可以有多个规格类目，一个规格类目下可以有多个规格值。
        tree: [],

        // 所有 sku 的组合列表，比如红色、M 码为一个 sku 组合，红色、S 码为另一个组合
        list: []
      }
    };
  },
  computed: {},
  created() {
    this.getList();
    this.countdown();
  },
  mounted() {},
  watch: {},
  methods: {
    getList() {
      this.$axios
        .get("http://api.deiyoudian.com/api/goods/buyer/v1/goods/get", {
          params: {
            id: 122326
          },
          headers: {
            "Content-Type": "application/json",
            Platform: "Y%2fnOm9Q6SjVaSxnxNdJgeQ%3d%3d",
            Intercept: "y9H9hVFbMnL3iurHI3HWlg%3d%3d",
            Environment:
              ""
          }
        })
        .then(res => {
          this.lists = res.data.response;
            this.sku.price = res.data.response.min_price 
          res.data.response.skus.forEach((item, index) => {
            this.sku.list.push({
              id: item.id,
              s1: index + "s1",
              s2: index + "s2",
              s3: index + "s3",
              price: item.price*100,
              stock_num: item.stock
            });

            item.goods_specs.forEach((v, vI) => {
              let keyId = this.sku.tree.find(t => {
                return t.k == v.key;
              });
              if (!keyId) {
                this.sku.tree.push({
                  k: v.key,
                  k_s: "s" + (vI + 1),
                  v: [
                    {
                      id: index + "s" + vI, // skuValueId：规格值 id
                      name: v.value, // skuValueName：规格值名称
                      imgUrl: v.picture // 规格类目图片，只有第一个规格类目可以定义图片
                      //   previewImgUrl: v.picture,// 用于预览显示的规格类目图片
                    }
                  ]
                });
              } else {
                let vId = keyId.v.find(q => {
                  return q.name == v.value;
                });
                if (!vId) {
                  keyId.v.push({
                    id: index + "s" + vI, // skuValueId：规格值 id
                    name: v.value, // skuValueName：规格值名称
                    imgUrl: v.picture
                  });
                }
              }
              let treeId = this.sku.tree.find(vt => {
                return vt.k == v.key;
              });
              if (treeId) {
                let valueId = treeId.v.find(ti => {
                  return ti.name == v.value;
                });
                if (valueId) {
                  this.sku.list[index]["s" + (vI + 1)] = valueId.id;
                }
              }
            });
          });
          
        })

        .catch(error => {});
     
    },
    // 点击购买回调	skuData: object
    onBuyClicked() {},
    // 点击添加购物车回调	skuData: object
    onAddCartClicked() {},

    // 购买商品
    onClickIcon() {
      Toast("点击图标");
    },
    onClickButton() {
      Toast("点击按钮");
      this.show = true;
    },

    // 倒计时
    countdown: function() {
      // 定义结束时间戳
      const end = Date.parse(new Date("2020-8-12 10:10:10"));
      // 定义当前时间戳
      const now = Date.parse(new Date());
      // 做判断当倒计时结束时都为0
      if (now >= end) {
        // this.day = 0;
        this.hr = 0;
        this.min = 0;
        this.sec = 0;
        return;
      }
      // 用结束时间减去当前时间获得倒计时时间戳
      const msec = end - now;
    //   let day = parseInt(msec / 1000 / 60 / 60 / 24); //算出天数
      let hr = parseInt((msec / 1000 / 60 / 60) % 24); //算出小时数
      let min = parseInt((msec / 1000 / 60) % 60); //算出分钟数
      let sec = parseInt((msec / 1000) % 60); //算出秒数
      //给数据赋值
    //   this.day = day;
      this.hr = hr > 9 ? hr : "0" + hr;
      this.min = min > 9 ? min : "0" + min;
      this.sec = sec > 9 ? sec : "0" + sec;
      //定义this指向
      const that = this;
      // 使用定时器 然后使用递归 让每一次函数能调用自己达到倒计时效果
      setTimeout(function() {
        that.countdown();
      }, 1000);
    }
  },
  components: {}
};
</script>

